import "core-js/modules/es.array.push.js";
import router from "@/router";
import { ref } from "@vue/reactivity";
import store from "@/store";
export default {
  setup() {
    const show = ref(false);
    const showPopup = () => {
      show.value = true;
    };
    //点击取消
    let returns = () => {
      // 获取时间
      const timeData = new Date();
      let yy = timeData.getFullYear();
      let mm = timeData.getMonth() + 1;
      let dd = timeData.getDate();
      let hh = timeData.getHours();
      let mf = timeData.getMinutes() < 10 ? "0" + timeData.getMinutes() : timeData.getMinutes();
      let ss = timeData.getSeconds() < 10 ? "0" + timeData.getSeconds() : timeData.getSeconds();
      mm = mm < 10 ? '0' + mm : mm;
      dd = dd < 10 ? '0' + dd : dd;
      // console.log(yy+"-"+mm+"-"+dd,hh+":"+mf+":"+ss);
      let time = `${yy}-${mm}-${dd} ${hh}:${mf}:${ss}`;
      //   console.log(time);
      //加入待支付type=1
      // 执行数据在readycar中加一个type=1
      store.commit("deltype", time);
      //取出allorder
      let allorder = JSON.parse(window.localStorage.getItem("allorder"));
      //判断是否有allorder，没有的话就直接存,有的话先取出再存
      //   console.log(allorder);
      if (allorder == null) {
        let aa = []; //空数组
        aa.push(store.state.oneorder); //aa中放入订单信息
        window.localStorage.setItem("allorder", JSON.stringify(aa)); //放入本地
      } else {
        let bb = JSON.parse(window.localStorage.getItem("allorder")); //取出数据
        bb.push(store.state.oneorder); //放入一个订单
        window.localStorage.setItem("allorder", JSON.stringify(bb)); //放入本地
      }

      router.push({
        path: "/myorder"
      });
    };
    // 点击确认
    let buynow = () => {
      // 获取时间
      const timeData = new Date();
      let yy = timeData.getFullYear();
      let mm = timeData.getMonth() + 1;
      let dd = timeData.getDate();
      let hh = timeData.getHours();
      let mf = timeData.getMinutes() < 10 ? "0" + timeData.getMinutes() : timeData.getMinutes();
      let ss = timeData.getSeconds() < 10 ? "0" + timeData.getSeconds() : timeData.getSeconds();
      mm = mm < 10 ? '0' + mm : mm;
      dd = dd < 10 ? '0' + dd : dd;
      // console.log(yy+"-"+mm+"-"+dd,hh+":"+mf+":"+ss);
      let time = `${yy}-${mm}-${dd} ${hh}:${mf}:${ss}`;

      //加入待发货type=2
      store.commit("buynow", time);
      //取出allorder
      let allorder = JSON.parse(window.localStorage.getItem("allorder"));
      //判断是否有allorder，没有的话就直接存,有的话先取出再存
      //   console.log(allorder);
      if (allorder == null) {
        let aa = []; //空数组
        aa.push(store.state.oneorder); //aa中放入订单信息
        window.localStorage.setItem("allorder", JSON.stringify(aa)); //放入本地
      } else {
        let bb = JSON.parse(window.localStorage.getItem("allorder")); //取出数据
        bb.push(store.state.oneorder); //放入一个订单
        window.localStorage.setItem("allorder", JSON.stringify(bb)); //放入本地
      }

      router.push({
        path: "/myorder"
      });
    };
    //获取title
    let title = ref("");
    title.value = store.state.readyCar[0].bannerData.title;
    // console.log(title.value);
    //获取总价格
    let money = JSON.parse(window.localStorage.getItem("allmoney"));

    // 返回上个界面
    let goBack = () => {
      router.go(-1);
    };
    return {
      goBack,
      money,
      title,
      showPopup,
      show,
      returns,
      buynow
    };
  }
};